* {margin: 0px;padding: 0px; box-sizing: border-box;}
.card {
  font-size: 18px;
  text-align: center;
  margin-bottom: 12px;
  cursor: pointer;
  perspective: 1000px;

  height: 33px;
  width: 170px;
  margin-right: 12px;


  &.done {
    .cardInner {
      transform: rotateY(180deg);
    }
  }
}
.root {
  display: flex;
  flex-direction: row;
}
.icons {
    margin: 10px 10px 10px 0px;
    cursor: pointer;
    &:hover {
      color: #1BB95E;
    }
  }
.cardInner {
  position: relative;
  background-color: transparent;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
 .isRemembered {
  .cardFront, .cardBack {
    text-decoration: line-through;
  }
}

.cardFront,
.cardBack {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  padding: 6px 12px;
}

.cardFront {
  background-color: #fff;
  color: black;
}

.cardBack {
  background-color: #1BB95E;
  color: white;
  transform: rotateY(180deg);
}